import React from "react";
import { GatsbyImage } from "gatsby-plugin-image";
import styled from "styled-components";

import breakpoints from "../layout/breakpoints";
import { Heading, Text } from "../base/typography";
import { BaseLink, TextLink } from "../../components/base/links";

const BlogItem = ({item}) => {

    return (
                <StyledBlogItem>
                    <BaseLink 
                        to={`/blog/${item.slug}`}
                    >
                        <StyledBlogCover>
                            <GatsbyImage 
                                image={item.image}
                                alt=""
                                layout="fullWidth" 
                            />
                        </StyledBlogCover>
                    </BaseLink>
                    <div>
                        <BaseLink 
                            to={`/blog/${item.slug}`}
                        >
                            <Heading 
                                level={4}
                            >
                                {item.title}
                            </Heading>
                        </BaseLink>
                        <Text 
                            noTopMargin
                        >
                            {item.content}
                        </Text>
                        <TextLink 
                            to={`/blog/${item.slug}`}
                        >
                            Read more
                        </TextLink> 
                    </div>
                </StyledBlogItem>
    )
};

const StyledBlogItem = styled.section`
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    max-width: 40rem;
    word-break: break-word;

    ${breakpoints.md`
        & {
            flex-direction: row;
        }
    `}
`;

const StyledBlogCover = styled.div`
    display: flex;
    max-height: 10rem;
    width: 100%;
    overflow: hidden;
    margin-bottom: var(--spacing-m);

    ${breakpoints.md`
        & {
            margin-bottom: 0;
            margin-right: var(--spacing-m);
            width: 10rem;
        }
    `}
`

export default BlogItem;