import React from "react";

import { LayoutItemWrapper } from "../layout/layout";
import { useStaticQuery, graphql } from 'gatsby';
import BlogItem from "./blog-item";

const BlogWrapper = () => {
    const data = useStaticQuery(graphql`
    query getBlogPosts {
        allStrapiPost(sort: {fields: publishedAt, order: DESC}) {
          nodes {
            title
            slug
            publishedAt(formatString: "DD-MM-YYYY")
            cover {
              localFile {
                childImageSharp {
                  gatsbyImageData(aspectRatio: 1)
                }
              }
            }
            content {
              data {
                childMdx {
                  excerpt
                }
              }
            }
          }
        }
      }     
    `);

    const posts = data.allStrapiPost.nodes.map((item, index) => {
        return {
            id: index,
            slug: item.slug,
            title: item.title,
            image: item.cover.localFile.childImageSharp.gatsbyImageData,
            content: item.content.data.childMdx.excerpt,
            publishedAt: item.publishedAt
        }
    });

    return (
        <LayoutItemWrapper 
            rowCenter 
            rowGap={'var(--spacing-m)'}
        > 
            {posts.map((item) => <BlogItem key={item.id} item={item} />)}
        </LayoutItemWrapper>
    )
}   

export default BlogWrapper;