import React from "react";
import Page from '../components/layout/page';
import { LayoutContainer } from '../components/layout/layout';
import BlogContent from "../components/page-content/blog/blog-content";

const Blog = () => {
    return (
        <>
        <Page active="blog">
            <LayoutContainer>
                <BlogContent />
            </LayoutContainer>
        </Page>
        </>
    )
};

export default Blog;